* {
  background-color: #eee;
  color: #000;
}

.pagination {
  justify-content: center;
  margin-top: 3rem;
}

.page-item {
  margin: 0 0.3rem;
}

.page-item .page-link {
  border: none;
  background-color: #eee;
}

.page-item.active .page-link {
  z-index: 1;
  color: #28a745 !important;
  background-color: #eee !important;
  border: none;
}

.list-group-item {
  background-color: #eee !important;
}

.list-group-item.active {
  z-index: 2;
  color: #fff;
  background-color: #28a745 !important;
  border: none;
}

i.fa-heart {
  color: #28a745;
}
